.title {
  margin: 60px 0px 0px 30px;
  font-family: 'AbrilFatFace';
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  line-height: 48.56px;
  color: white;
}

.description {
  margin: 20px 0px 0px 0px;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 15px;
  font-style: normal;
  font-weight: 400;
  line-height: 21.72px;
  color: #96DE98;
  display: flex;
  overflow-x: scroll;
  white-space: nowrap;
}

.description::-webkit-scrollbar {
  display: none;
}

.content {
  margin: 30px 0px;
}

.scrollableSection {
  display: flex;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
  scroll-padding-inline: calc(100% / 2 - 115px);
  margin-top: 30px;
  padding: 0px 30px 0px 10px;
  scroll-behavior: smooth;
}

.scrollableSection>.item {
  scroll-snap-align: center;
  scroll-snap-stop: always;
}

.scrollableSection::-webkit-scrollbar {
  display: none;
}

@media (width >=450px) {
  .content {
    margin: 60px 0px 0px;
    padding-bottom: 30px;
    height: calc(100vh - 90px - 60px);
    max-height: calc(852px - 30px - 60px);
    overflow: auto;
    border-radius: 60px;
  }

  .content::-webkit-scrollbar {
    display: none;
  }

  Header {
    margin-top: 60px;
  }
}

@font-face {
  font-family: "AbrilFatFace";
  src: url("../fonts/AbrilFatface-Regular.ttf");
}